                        <el-table-column prop="activityTime" label="" width="100"> </el-table-column>
                        <el-table-column prop="endTime" label="" width="120"></el-table-column>
                        <el-table-column prop="enrolment" label="" width="80"></el-table-column>
                        <el-table-column prop="ticketsNumber" label="" width="120"> </el-table-column>
                        <el-table-column prop="visitorsNumber" label="" width="120"> </el-table-column>
                        <el-table-column prop="evaluationScore" label="" width="120"> </el-table-column>
                        <el-table-column prop="state" label="" width="120"> </el-table-column>
                        <el-table-column label="" width="240" fixed="right">
                            <template slot-scope="scope">
                                <el-button size="small" type="text">
                                    <el-dropdown trigger="click">
                                    <span class="el-dropdown-link">
                                        
                                    </span>
                                    <el-dropdown-menu slot="dropdown" type="text">
