            <div class="user_footer" slot="footer">
                <el-row class="row_e">
                <el-button class="btn_deletes" size="small" @click="cancelVisible = false"></el-button>
                <el-button type="primary" size="small"  class="btn_delete"></el-button>
                </el-row>
            </div>
        </el-dialog>
        <!--  -->
        <el-dialog :visible.sync="remarkVisible" width="400px">
            <div slot="title" class="dialog_delete_head_title">
                <i class="iconfont icon-edit-square delete_icon"></i>
                <span></span>
            </div>
            <div class="remark_content">
            <p class="remark_p"></p>
