            <div class="user_footer" slot="footer">
            <el-row class="row_e">
                <el-button class="btn_deletes" size="small" @click="remarkVisible = false"></el-button>
                <el-button type="primary" size="small"  class="btn_delete" ></el-button>
            </el-row>
            </div>
        </el-dialog>
        <!--  -->
        <el-dialog :visible.sync="deleteVisible" width="300px">
            <div slot="title" class="dialog_delete_head_title">
                <i class="iconfont icon-trash delete_icon"></i>
                <span></span>
            </div>
            <div class="delete_content">
                <img class="boolean_delete" :src="warmImg"/>
                <p></p>
            </div>
            <div class="user_footer" slot="footer">
            <el-row class="row_e">
                <el-button class="btn_deletes" size="small" @click="deleteVisible = false"></el-button>
                <el-button type="primary" size="small"  class="btn_delete" ></el-button>
            </el-row>
            </div>
