                    id: 1,
                    activityName: '',
                    city:'',
                    companyName: '',
                    activityTime:'2018.9.29',
                    endTime:'2018.10.01',
                    enrolment:'45',
                    ticketsNumber:'99',
                    visitorsNumber:'123',
                    evaluationScore:'100',
                    state:'',
                   
                },{
                   id: 2,
                    activityName: '',
                    city:'',
                    companyName: '',
                    activityTime:'2018.9.29',
                    endTime:'2018.10.01',
                    enrolment:'555',
                    ticketsNumber:'99',
                    visitorsNumber:'123',
                    evaluationScore:'100',
                    state:'',
                },{
                   id: 3,
                    activityName: '',
                    city:'',
                    companyName: '',
                    activityTime:'2018.9.29',
                    endTime:'2018.10.01',
                    enrolment:'45',
                    ticketsNumber:'99',
                    visitorsNumber:'123',
                    evaluationScore:'100',
                    state:'',
                }],
                historyData: [{
                    id: 1,
                    name: '',
                    createTime: '2018/08/07',
                    message: ''
                },{
                    id: 2,
                    name: '',
                    createTime: '2018/08/07',
                    message: ''
                },{
                    id: 3,
                    name: '',
                    createTime: '2018/08/07',
                    message: ''
                },{
                    id: 4,
                    name: '',
                    createTime: '2018/08/07',
                    message: ''
                }]
            }
        },
        mounted(){
          
        },
        methods:{
           
